import { useLocation, useNavigate } from 'react-router-dom';
import {
  BottomBlock,
  ExpiryDate,
  NavItem,
  NavItems,
  SidebarWrapper,
  UpdateButton,
} from './styled';

import dashboardIcon from '../../../images/sidebar/dashboard.svg';
import seachIcon from '../../../images/sidebar/search.svg';
import settingsIcon from '../../../images/sidebar/settings.svg';
import supportIcon from '../../../images/sidebar/support.svg';
import { useAppContext } from '../../storage/context';
import { SidebarBanner } from '../Banners/SidebarBanner';

const LINKS = [
  {
    title: 'Dashboard',
    path: '/',
    icon: <img src={dashboardIcon} />,
  },
  {
    title: 'Search',
    path: '/search',
    icon: <img src={seachIcon} />,
  },
  {
    title: 'Settings',
    path: '/settings',
    icon: <img src={settingsIcon} />,
  },
  {
    title: 'Support',
    path: '/support',
    icon: <img src={supportIcon} />,
  },
];

const Sidebar = () => {
  const { state } = useAppContext();
  const location = useLocation();
  const navigate = useNavigate();

  return (
    <SidebarWrapper>
      <NavItems>
        {LINKS.map((item) => {
          return (
            <NavItem
              key={item.title}
              onClick={() => navigate(item.path)}
              active={location.pathname === item.path ? 1 : 0}
            >
              {item.icon}
              <div>{item.title}</div>
            </NavItem>
          );
        })}
      </NavItems>
      <BottomBlock>
        {state.isDemo ? (
          <SidebarBanner />
        ) : (
          <ExpiryDate>
            <p>
              <strong>{state.licenceExpiresAt}</strong>
            </p>
            <p className="licence">licence expires</p>
          </ExpiryDate>
        )}
        <UpdateButton onClick={() => navigate('/license')}>
          <p>Upgrade Software</p>
        </UpdateButton>
      </BottomBlock>
    </SidebarWrapper>
  );
};

export default Sidebar;
