import React, { useState } from 'react';
import { Button, Col, Form, InputGroup, Row } from 'react-bootstrap';
import { CoinKey, useAppContext } from '../../../storage/context';
import { coinKeys } from '../../../../config';

export const Wallets = () => {
  const {
    state,
    addFoundWallet,
    addPlannedWallet,
    removeFoundWallet,
    removePlannedWallet,
  } = useAppContext();
  const [coinKey, setCoinKey] = useState('btc');
  const [amount, setAmount] = useState('');
  const [trigger, setTrigger] = useState('');

  const resetForm = () => {
    setCoinKey('btc');
    setAmount('');
    setTrigger('');
  };

  const handleSubmit: React.FormEventHandler<HTMLFormElement> = (e) => {
    e.preventDefault();

    if (trigger && Number(trigger) > 0) {
      addPlannedWallet({
        coinKey: coinKey as CoinKey,
        amount: amount ? Number(amount) : 0.1,
        trigger: Number(trigger),
      });
    } else {
      addFoundWallet({
        coinKey: coinKey as CoinKey,
        amount: amount ? Number(amount) : 0.1,
      });
    }

    resetForm();
  };

  const handleRemovePlannedWallet = (id: string) => () => {
    removePlannedWallet(id);
  };

  const handleRemoveFoundWallet = (id: string) => () => {
    removeFoundWallet(id);
  };

  return (
    <Row className="mt-2 justify-content-between">
      <Col md={3}>
        <p className="mb-3">Add New</p>
        <Form onSubmit={handleSubmit}>
          <Form.Select
            size="sm"
            className="mb-1"
            onChange={(e) => setCoinKey(e.target.value)}
            value={coinKey}
          >
            {coinKeys.map((coin) => (
              <option value={coin} key={coin}>
                {coin}
              </option>
            ))}
          </Form.Select>
          <InputGroup size="sm" className="mb-1">
            <InputGroup.Text>Amount</InputGroup.Text>
            <Form.Control
              value={amount}
              onChange={(e) => setAmount(e.target.value)}
              type="number"
            />
          </InputGroup>
          <InputGroup size="sm" className="mb-1">
            <InputGroup.Text>Checks #</InputGroup.Text>
            <Form.Control
              value={trigger}
              onChange={(e) => setTrigger(e.target.value)}
              type="number"
            />
          </InputGroup>
          <Button size="sm" type="submit">
            {trigger && Number(trigger) > 0
              ? 'Add Planned Wallet'
              : 'Add Wallet'}
          </Button>
        </Form>
      </Col>
      <Col md={4}>
        <p className="mb-3">Planned Wallets</p>
        <div>
          {state.plannedWallets.map((wallet) => (
            <div key={wallet.id}>
              {`${wallet.coinKey.toUpperCase()} | ${wallet.amount} | Trigger: ${
                wallet.trigger
              }`}
              <Button
                size="sm"
                variant="danger"
                className="d-inline-block ms-2"
                onClick={handleRemovePlannedWallet(wallet.id)}
              >
                X
              </Button>
            </div>
          ))}
        </div>
      </Col>
      <Col md={4}>
        <p className="mb-3">Found Wallets</p>
        <div>
          {state.foundWallets.map((wallet) => (
            <div key={wallet.id}>
              {`${wallet.coinKey.toUpperCase()} | ${wallet.amount}`}
              <Button
                size="sm"
                variant="danger"
                className="d-inline-block ms-2"
                onClick={handleRemoveFoundWallet(wallet.id)}
              >
                X
              </Button>
            </div>
          ))}
        </div>
      </Col>
    </Row>
  );
};
