import { CoinKey, useAppContext } from './context';

export const useUpdaters = () => {
  const { state, updateState } = useAppContext();

  const updateCoinsFoundTotal = (
    key: CoinKey,
    value: string | number | null,
  ) => {
    const updatedValues = {
      ...state.foundCoins,
      [key]: value ?? 0,
    };
    updateState('foundCoins', updatedValues);
  };

  return {
    updateCoinsFoundTotal,
  };
};
