import styled from 'styled-components';
import { colors } from '../../../theme/colors';

export const Bar = styled.div`
  position: relative;
`;

export const ScrollContainer = styled.div`
  position: relative;
  margin: 20px 0;
  width: 100%;
  max-width: 100%;
  overflow: hidden;
`;

export const ScrollItems = styled.div`
  display: flex;
  flex-wrap: nowrap;
`;

interface ScrollItemProps {
  active: number;
  disabled: boolean;
}

export const ScrollItem = styled.div<ScrollItemProps>`
  cursor: ${(props) => (props.disabled ? 'not-allowed' : 'pointer')};
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 15px 0 15px 0;
  min-width: 120px;
  margin-right: 10px;
  background: ${(props) =>
    props.active ? colors.greenTile : colors.alterGrey};
  border-radius: 10px;

  .title {
    margin-top: 10px;
    color: ${(props) =>
      props.active ? colors.activeGreen : colors.disabledTileText};
  }

  .marker {
    position: absolute;
    bottom: 10px;
    right: 10px;
    width: 20px;
    height: 20px;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: ${(props) => (props.active ? '#4a5f5d' : '#4b4d58')};

    span {
      display: block;
      border-radius: 100%;
      width: 10px;
      height: 10px;
      background: ${(props) => (props.active ? colors.activeGreen : '#363636')};
    }
  }
`;

export const Image = styled.img`
  display: block;
  width: 36px;
  height: 36px;
`;

export const ScrollButton = styled.div`
  cursor: pointer;
  position: absolute;
  bottom: 0;
  height: 100%;
  width: 50px;
  background: ${colors.mainGrey};
  z-index: 4;

  &::after {
    content: '';
    position: absolute;
    bottom: 50%;
    left: 50%;
    width: 20px;
    height: 20px;
    border: 2px solid #fff;
    border-left: 0;
    border-bottom: 0;
    z-index: 5;
  }

  @media (min-width: 1550px) {
    display: none;
  }
`;

interface ScrollRightProps {
  disabled: boolean;
}

export const ScrollRight = styled(ScrollButton)<ScrollRightProps>`
  right: 0;

  // for disabled button
  cursor: ${(props) => (props.disabled ? 'default' : 'pointer')};

  &::after {
    transform: translate(-50%, 50%) rotate(45deg);

    // for disabled button
    opacity: ${(props) => (props.disabled ? '0.3' : '1')};
  }

  // to fill space for unavailable banner
  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: -20px;
    width: 20px;
    height: 100%;
    background-color: ${colors.mainGrey};
  }
`;

export const ScrollLeft = styled(ScrollButton)`
  left: 0;

  &::after {
    transform: translate(-50%, 50%) rotate(-135deg);
  }
`;
