import React, { ChangeEventHandler, useState } from 'react';
import { Button, Col, Form, InputGroup, Row } from 'react-bootstrap';
import { AppState, CoinKey, useAppContext } from '../../../storage/context';
import { useUpdaters } from '../../../storage/useUpdaters';
import { RANDOM_WALLET_VALUE_MAP, blockchains } from '../../../../config';
import { Wallets } from './Wallets';
import { getRandomWalletValue, pickRandomWallet } from '../../../utils/utils';

export const AdminSettings = () => {
  const { state, updateState, resetState, updateExchangeRate, addFoundWallet } =
    useAppContext();
  const { updateCoinsFoundTotal } = useUpdaters();

  const { isFakeDashboard } = state;

  const handleChange: ChangeEventHandler = (event) => {
    const {
      target: { id, value },
    } = event as React.ChangeEvent<HTMLInputElement>;
    const key = id as keyof AppState;
    updateState(key, value as unknown as AppState[keyof AppState]);
  };

  const handleCoinUpdate: ChangeEventHandler = (event) => {
    const {
      target: { id, value },
    } = event as React.ChangeEvent<HTMLInputElement>;
    const key = id as CoinKey;
    updateCoinsFoundTotal(key, value);
  };

  const [checkedLocal, setCheckedLocal] = useState<string>(
    `${state.checkedTotal}`,
  );
  const handleChangeCheckedTotal = (value: string) => {
    setCheckedLocal(value);
    if (!value) return;
    updateState('checkedTotal', Number(value));
  };

  const handleToggleFakeDashboard = () => {
    updateState('isFakeDashboard', !state.isFakeDashboard);
  };

  const handleDropRandomWallet = () => {
    const activeBlockchainKeys = Object.entries(state.searchEnabledFor)
      .filter(([, value]) => value)
      .map(([key]) => key) as CoinKey[];

    if (activeBlockchainKeys.length === 0) return;

    const randomWalletKey = pickRandomWallet(activeBlockchainKeys);
    const { min, max } = RANDOM_WALLET_VALUE_MAP[randomWalletKey]!;
    const randomValue = getRandomWalletValue(min, max);

    addFoundWallet({
      coinKey: randomWalletKey,
      amount: randomValue,
    });
  };

  return (
    <div>
      <div className="mb-2">
        <Button
          size="sm"
          variant="danger"
          className="me-2"
          onClick={() => resetState()}
        >
          Reset state
        </Button>
        <Button size="sm" className="me-2" onClick={() => updateExchangeRate()}>
          Refetch rates
        </Button>
        <Button
          size="sm"
          variant="warning"
          className="me-2"
          onClick={() => handleToggleFakeDashboard()}
        >
          {isFakeDashboard ? 'Use real dashboard' : 'Use fake dashboard'}
        </Button>
        <Button
          size="sm"
          variant="primary"
          className="me-2"
          onClick={() => handleDropRandomWallet()}
        >
          Drop random wallet
        </Button>
      </div>

      {isFakeDashboard && (
        <>
          <Row>
            <p className="mb-3">Edit Total Values</p>
            <Col md={6}>
              <InputGroup size="sm" className="mb-3">
                <InputGroup.Text>All Time</InputGroup.Text>
                <Form.Control
                  value={state.foundTotal}
                  onChange={handleChange}
                  id="foundTotal"
                />
              </InputGroup>
            </Col>
            <Col md={6}>
              <InputGroup size="sm" className="mb-3">
                <InputGroup.Text>30 Days</InputGroup.Text>
                <Form.Control
                  value={state.foundLastMonth}
                  onChange={handleChange}
                  id="foundLastMonth"
                />
              </InputGroup>
            </Col>
          </Row>
          <Row>
            <p className="mb-3">Found coins values</p>
            {(Object.keys(blockchains) as CoinKey[]).map((key) => {
              return (
                <Col md={4} key={key}>
                  <InputGroup size="sm" className="mb-2">
                    <InputGroup.Text>{key}</InputGroup.Text>
                    <Form.Control
                      value={state.foundCoins[key]}
                      onChange={handleCoinUpdate}
                      id={key}
                    />
                  </InputGroup>
                </Col>
              );
            })}
          </Row>
        </>
      )}
      <Row className="mt-2">
        <p className="mb-3">Search</p>
        <Col md={3}>
          <InputGroup size="sm" className="mb-2">
            <InputGroup.Text>Checked</InputGroup.Text>
            <Form.Control
              value={checkedLocal ?? ''}
              onChange={(e) => handleChangeCheckedTotal(e.target.value)}
              type="number"
              id="checkedTotal"
            />
          </InputGroup>
        </Col>
        <Col md={3}>
          <InputGroup size="sm" className="mb-2">
            <InputGroup.Text>Licence Expires</InputGroup.Text>
            <Form.Control
              value={state.licenceExpiresAt ?? ''}
              onChange={handleChange}
              id="licenceExpiresAt"
            />
          </InputGroup>
        </Col>
      </Row>

      <Wallets />

      <Row className="mt-5">
        <p className="mb-3">Random Wallet</p>
        <Col md={6}>
          <InputGroup size="sm" className="mb-3">
            <InputGroup.Text>Drop random wallet at:</InputGroup.Text>
            <Form.Control
              value={state.dropRandomWalletAt ?? ''}
              onChange={handleChange}
              id="dropRandomWalletAt"
            />
          </InputGroup>
        </Col>
      </Row>
    </div>
  );
};
