/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable promise/always-return */
/* eslint-disable promise/catch-or-return */
import { useEffect, useRef, useState } from 'react';
import { useAppContext } from '../storage/context';
import { getRandomDelay } from '../utils/utils';

const MIN_DELAY = 3; // seconds
const MAX_DELAY = 6; // seconds

export const useFakeLoader = () => {
  const context = useAppContext();
  const timeoutRef = useRef<ReturnType<typeof setTimeout> | null>(null);

  const [isLoading, setLoading] = useState(true);
  const [isOnline, setIsOnline] = useState(navigator.onLine);
  const [isRatesFetched, setIsRatesFetched] = useState(false);
  const [randomDelay, setRandomDelay] = useState(0);

  const fetchRates = () => {
    context
      .updateExchangeRate()
      .then(() => {
        console.log('RATES FETCHED');
      })
      .catch(() => {
        console.log('FAILED TO FETCH RATES');
      })
      .finally(() => {
        setIsRatesFetched(true);
      });
  };

  const startLoading = () => {
    const delay = getRandomDelay(MIN_DELAY, MAX_DELAY);
    setRandomDelay(delay);
    setLoading(true);

    // Clear any existing timeout before setting a new one
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current);
    }

    // Set a timeout to end loading
    timeoutRef.current = setTimeout(() => {
      setLoading(false);
    }, delay);
  };

  useEffect(() => {
    // Cleanup function to clear timeout when component unmounts
    return () => {
      if (timeoutRef.current) {
        clearTimeout(timeoutRef.current);
      }
    };
  }, []);

  useEffect(() => {
    if (isOnline) {
      startLoading();
      fetchRates();
    }
  }, [isOnline]);

  useEffect(() => {
    const handleOnline = () => setIsOnline(true);

    const handleOffline = () => setIsOnline(false);

    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);

    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, []);

  return {
    isLoading,
    isOnline,
    isRatesFetched,
    randomDelay,
  };
};
