interface ActivateButtonProps {
  isDisabled: boolean;
  isLoading: boolean;
}

export const ActivateButton = styled.div<ActivateButtonProps>`
  margin-top: 15px;
  padding: 20px 15px;
  border-radius: 15px;
  font-size: 16px;
  color: white;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;

  cursor: ${(props) =>
    props.isDisabled || props.isLoading ? 'default' : 'pointer'};
  background: ${(props) => (props.isDisabled ? '#4e4f5a' : colors.activeBlue)};
  color: ${(props) => (props.isDisabled ? '#979797' : '#ffffff')};

  img {
    margin-left: 10px;
    display: inline-block;
    width: 18px;
  }

  .spinner {
    margin-left: 10px;
    width: 20px;
    height: 20px;
    border: 2px solid #fff; /* Light border */
    border-top: 2px solid rgba(0, 0, 0, 0); /* Darker border to show spinning */
    border-radius: 50%;
    animation: spin 1s linear infinite;
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
`;

interface CloseButtonProps {
  isDisabled: boolean;
}

export const CloseButton = styled.div<CloseButtonProps>`
  cursor: ${(props) => (props.isDisabled ? 'default' : 'pointer')};
  opacity: ${(props) => (props.isDisabled ? 0.3 : 1)};
  position: absolute;
  right: 20px;
  top: 20px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #2a2c39;

  &::before,
  &::after {
    position: absolute;
    top: 50%;
    left: 50%;
    content: ' ';
    height: 25px;
    width: 2px;
    background-color: #94959d;
  }
  &::before {
    transform: translateY(-50%) translateX(-50%) rotate(45deg);
  }
  &::after {
    transform: translateY(-50%) translateX(-50%) rotate(-45deg);
  }
`;
