import { useRef, useState } from 'react';
import { blockchains } from '../../../../config';

import BTCIcon from '../../../../images/coins/BTC.png';
import BNBIcon from '../../../../images/coins/BNB.png';
import DOGEIcon from '../../../../images/coins/DOGE.png';
import ETHIcon from '../../../../images/coins/ETH.png';
import LTCIcon from '../../../../images/coins/LTC.png';
import SOLIcon from '../../../../images/coins/SOL.png';
import TONIcon from '../../../../images/coins/TON.png';
import TRXIcon from '../../../../images/coins/TRX.png';
import XRPIcon from '../../../../images/coins/XRP.png';

import {
  Bar,
  ScrollContainer,
  ScrollItem,
  ScrollItems,
  Image,
  ScrollLeft,
  ScrollRight,
} from './styled';
import { CoinKey, useAppContext } from '../../../storage/context';
import { BlockchainSelectBanner } from '../../Banners/BlockchainSelectBanner';

export const IMAGES = {
  btc: BTCIcon,
  bnb: BNBIcon,
  doge: DOGEIcon,
  eth: ETHIcon,
  ltc: LTCIcon,
  sol: SOLIcon,
  ton: TONIcon,
  trx: TRXIcon,
  xrp: XRPIcon,
};

export const BlockchainSelector = () => {
  const containerRef = useRef<HTMLDivElement>(null);
  const [isScrolled, setIsScrolled] = useState(false);
  const {
    state,
    updateState,
    search: { isRunning },
  } = useAppContext();
  const { isDemo } = state;

  const handleToggle = (key: CoinKey) => {
    if (isRunning) return;

    const updatedFilters = {
      ...state.searchEnabledFor,
      [key]: !state.searchEnabledFor[key],
    };

    updateState('searchEnabledFor', updatedFilters);
  };

  const handleSrollLeft = () => {
    if (isDemo) return;

    if (containerRef.current) {
      containerRef.current.scrollTo({
        left: 0,
        behavior: 'smooth',
      });
      setIsScrolled(false);
    }
  };

  const handleSrollRight = () => {
    if (isDemo) return;

    if (containerRef.current) {
      containerRef.current.scrollTo({
        left: containerRef.current.scrollWidth,
        behavior: 'smooth',
      });
      setIsScrolled(true);
    }
  };

  return (
    <Bar>
      {isDemo && <BlockchainSelectBanner />}
      {isScrolled && <ScrollLeft onClick={handleSrollLeft} />}
      {!isScrolled && (
        <ScrollRight onClick={handleSrollRight} disabled={isDemo} />
      )}
      <ScrollContainer ref={containerRef}>
        <ScrollItems>
          {Object.entries(blockchains).map(([key, blockchain]) => {
            const isActive = state.searchEnabledFor[key as CoinKey] ? 1 : 0;
            return (
              <ScrollItem
                key={key}
                active={isActive}
                disabled={isRunning}
                onClick={() => handleToggle(key as CoinKey)}
              >
                <Image src={IMAGES[key as keyof typeof IMAGES]} />
                <p className="title">{blockchain.title}</p>
                <div className="marker">
                  <span />
                </div>
              </ScrollItem>
            );
          })}
        </ScrollItems>
      </ScrollContainer>
    </Bar>
  );
};
