/* eslint-disable no-promise-executor-return */
import { useNavigate } from 'react-router-dom';
import { ChangeEvent, useState } from 'react';
import {
  CloseButton,
  Content,
  Wrapper,
  Title,
  ChatBox,
  ChatButton,
  Banners,
  NoteBanner,
  InputContainer,
  StyledInput,
  LicenseSection,
  ActivateButton,
} from './styled';
import tgIcon from '../../../images/sidebar/tg.svg';
import { getRandomDelay } from '../../utils/utils';
import ErrorSnackbar from './ErrorSnackbar';
import { TELEGRAM_LINK } from '../../../config';

export const License = () => {
  const navigate = useNavigate();

  const [value, setValue] = useState('');
  const [isLoading, setLoading] = useState(false);
  const [snackbarVisible, setSnackbarVisible] = useState<boolean>(false);
  const isButtonDisabled = value.length !== 19;

  const handleGoBack = () => {
    if (isLoading) return;
    navigate(-1);
  };

  const showSnackbar = () => {
    setSnackbarVisible(true);
    setTimeout(() => {
      setSnackbarVisible(false);
    }, 5000); // Automatically hide after X seconds
  };

  const startActivation = async () => {
    setLoading(true);
    const delay = getRandomDelay(4, 7);
    await new Promise((resolve) => setTimeout(resolve, delay));
    setLoading(false);
    setValue('');
    showSnackbar();
  };

  const formatLicenseKey = (input: string) => {
    const cleaned = input.replace(/[^a-zA-Z0-9]/g, ''); // Remove non-alphanumeric characters
    const chunked = cleaned.match(/.{1,4}/g); // Split into chunks of 4 characters
    return chunked ? chunked.join('-') : '';
  };

  const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
    if (isLoading) return;
    const formattedValue = formatLicenseKey(e.target.value);
    setValue(formattedValue);
  };

  const handleActivate = () => {
    if (isLoading || isButtonDisabled) return;
    startActivation();
  };

  return (
    <Wrapper>
      <CloseButton onClick={handleGoBack} isDisabled={isLoading} />
      <Content>
        <Title>Set Up License Key</Title>
        <Banners>
          <ChatBox>
            <div className="text">
              <p className="main">
                To BUY the upgrade license key follow the link:
              </p>
              <p>
                <a
                  className="link"
                  href={TELEGRAM_LINK.url}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  {TELEGRAM_LINK.label}
                </a>{' '}
                or click on the redirect button.
              </p>
            </div>
            <ChatButton
              href={TELEGRAM_LINK.url}
              target="_blank"
              rel="noopener noreferrer"
            >
              <img src={tgIcon} /> <div>Open Telegram</div>
            </ChatButton>
          </ChatBox>
          <NoteBanner>
            <strong>
              Our project is not responsible for any payments made to
              third-party services or interactions
            </strong>{' '}
            with unofficial pages. Please use only our verified channel for
            communication at{' '}
            <a
              className="link"
              href={TELEGRAM_LINK.url}
              target="_blank"
              rel="noopener noreferrer"
            >
              {TELEGRAM_LINK.label}
            </a>
            .
          </NoteBanner>
        </Banners>
        <LicenseSection>
          <InputContainer>
            <StyledInput
              type="text"
              value={value}
              onChange={handleChange}
              placeholder="xxxx-xxxx-xxxx-xxxx"
              maxLength={19}
              spellCheck="false"
              disabled={isLoading}
            />
          </InputContainer>
          <ActivateButton
            isDisabled={isButtonDisabled}
            isLoading={isLoading}
            onClick={() => handleActivate()}
          >
            <span>Activate License Key</span>
            {isLoading && <span className="spinner" />}
          </ActivateButton>
        </LicenseSection>
      </Content>
      <ErrorSnackbar visible={snackbarVisible} />
    </Wrapper>
  );
};
