/* eslint-disable react-hooks/exhaustive-deps */
import { Col, Row } from 'react-bootstrap';
// import { useEffect } from 'react';
import {
  Heading,
  TotalTiles,
  Tile,
  Section,
  CryptoTiles,
  CryptoTile,
} from './styled';
import { GreenIcon } from '../shared/GreenIcon';
import { blockchains } from '../../../config';
import { CoinKey, useAppContext } from '../../storage/context';
import { calculateCoinTotal, calculateTotal } from '../../utils/utils';
import { DemoMark } from '../shared/DemoMark/intex';
import { DashboardBanner } from '../Banners/DashboardBanner';

export const Dashboard = () => {
  const { state } = useAppContext();
  const { isFakeDashboard, foundWallets, exchangeRate } = state;

  // refetch rates on Dashboard mout
  // useEffect(() => {
  //   updateExchangeRate();
  // }, []);

  const getFoundTotal = () => {
    if (isFakeDashboard) return state.foundTotal;
    return calculateTotal(foundWallets, exchangeRate);
  };

  const getFoundLastMonth = () => {
    if (isFakeDashboard) return state.foundLastMonth;
    return calculateTotal(foundWallets, exchangeRate);
  };

  const getCoinTotal = (key: CoinKey) => {
    if (isFakeDashboard) return state.foundCoins[key as CoinKey];
    return calculateCoinTotal(key, foundWallets);
  };

  return (
    <div>
      <Section>
        <Heading>
          <p className="title">
            <span>Dashboard</span> <DemoMark />
          </p>
          <p className="subtitle">Statistics on blockchain discovered</p>
        </Heading>
        <TotalTiles>
          <Row>
            <Col sm={6}>
              <Tile>
                <div className="total">${getFoundTotal()}</div>
                <div className="desc">in all the time</div>
                <div className="icon">
                  <GreenIcon />
                </div>
              </Tile>
            </Col>
            <Col sm={6}>
              <Tile>
                <div className="total">${getFoundLastMonth()}</div>
                <div className="desc">in the last 30 days</div>
                <div className="icon">
                  <GreenIcon />
                </div>
              </Tile>
            </Col>
          </Row>
        </TotalTiles>
      </Section>
      <Section>
        <Heading>
          <p className="title">
            <span>Blockchain</span> <DemoMark />
          </p>
          <p className="subtitle">Statistics on specific blockchain</p>
        </Heading>
        <CryptoTiles>
          <Row>
            {Object.entries(blockchains).map(([key, bch]) => {
              return (
                <Col sm={4} key={key}>
                  <CryptoTile color={bch.color}>
                    <div className="sum-block">
                      <span className="total">
                        {getCoinTotal(key as CoinKey) || '0.0'}
                      </span>
                      <span className="currency">{bch.title}</span>
                    </div>
                    <div className="desc">in the last 30 days</div>
                  </CryptoTile>
                </Col>
              );
            })}
          </Row>
          {state.isDemo && <DashboardBanner />}
        </CryptoTiles>
      </Section>
    </div>
  );
};
