import styled from 'styled-components';
import { useAppContext } from '../../../storage/context';

const Mark = styled.span`
  font-size: 9px;
  font-weight: 700;
  background: #ffc700;
  color: #000000;
  padding: 2px 12px;
  height: 18px;
  border-radius: 10px;
  vertical-align: center;
  margin-left: 10px;
`;

export const DemoMark = () => {
  const {
    state: { isDemo },
  } = useAppContext();

  if (!isDemo) return null;

  return <Mark>FREE TRIAL VERSION</Mark>;
};
