import { useEffect, useState } from 'react';
import { LoaderContainer, LoadingText, Overlay } from './styled';

interface Props {
  delay: number;
}

const LOADING_TEXTS = [
  'Connecting to server...',
  'Setting up proxy...',
  'Starting daemon...',
];

const getTextChangeInterval = (delay: number, textCount: number) => {
  return delay / textCount;
};

export const LoadingScreen = ({ delay }: Props) => {
  const [loadingText, setLoadingText] = useState(LOADING_TEXTS[0]);

  useEffect(() => {
    if (delay > 0) {
      const textChangeInterval = getTextChangeInterval(
        delay,
        LOADING_TEXTS.length,
      );

      let textIndex = 0;
      setLoadingText(LOADING_TEXTS[textIndex]);

      const intervalId = setInterval(() => {
        textIndex = (textIndex + 1) % LOADING_TEXTS.length;
        setLoadingText(LOADING_TEXTS[textIndex]);
      }, textChangeInterval);

      setTimeout(() => {
        clearInterval(intervalId);
      }, delay);
    }
  }, [delay]);

  return (
    <Overlay>
      <LoaderContainer>
        <div className="loader" />
      </LoaderContainer>
      <LoadingText>{loadingText}</LoadingText>
    </Overlay>
  );
};
