/* eslint-disable jsx-a11y/click-events-have-key-events */
import { Col, Row } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { Overlay } from '../../shared/Overlay';
import {
  Banner,
  CloseIcon,
  CopyButton,
  Modal,
  Title,
  Word,
  Words,
} from './styled';
import { useAppContext } from '../../../storage/context';
import copyIcon from '../../../../images/sidebar/copy.svg';
import keyIcon from '../../../../images/sidebar/key.svg';

interface Props {
  walletId: string;
  onClose: () => void;
}

export const WithdrawModal = ({ walletId, onClose }: Props) => {
  const { state } = useAppContext();
  const navigate = useNavigate();
  const wallet = state.foundWallets.find((w) => w.id === walletId)!;
  const phraseWords = wallet.phrase.split(' ');

  return (
    <Overlay>
      <Modal>
        <CloseIcon onClick={onClose} />
        <Title>Seed Phrase</Title>
        <Words>
          <Row>
            {phraseWords.map((word, i) => {
              const orderNumber = i + 1;
              const isHidden = orderNumber > 7;

              return (
                <Col sm={4} key={word}>
                  <Word isHidden={isHidden}>
                    <span className="order">{orderNumber}</span>
                    <span className="word">{isHidden ? 'hidden' : word}</span>
                  </Word>
                </Col>
              );
            })}
          </Row>
        </Words>
        <Banner>
          <div className="title">
            Seed Phrase is locked <img src={keyIcon} />
          </div>
          <div className="note">
            Upgrade your current plan using a valid license key{' '}
            <span className="link" onClick={() => navigate('/license')}>
              here
            </span>
            .
          </div>
        </Banner>
        <Row className="justify-content-center">
          <CopyButton>
            <span>Copy Phrase</span>
            <img src={copyIcon} />
          </CopyButton>
        </Row>
      </Modal>
    </Overlay>
  );
};
