/* eslint-disable import/no-cycle */
import { MAX_CHECKS_COUNT, MIN_CHECKS_COUNT, coinSymbols } from '../../config';
import {
  CoinKey,
  ExchangeRate,
  FoundWallet,
  initialExchangeRate,
} from '../storage/context';

interface Rate {
  symbol: string;
  price_usd: string;
}

export const fetchCoinRates = async () => {
  const endpoint = 'https://api.coinlore.net/api/tickers/';
  const res = await fetch(endpoint)
    .then((data) => data.json())
    .catch((error) => console.log(error));

  if (res?.data) {
    const rates = res.data.filter((rate: Rate) =>
      coinSymbols.includes(rate.symbol),
    );

    const normalizedData: ExchangeRate = { ...initialExchangeRate };
    rates.forEach((rate: Rate) => {
      normalizedData[rate.symbol.toLowerCase() as CoinKey] = rate.price_usd;
    });
    return normalizedData;
  }

  return null;
};

export const getUsdValue = (
  rates: ExchangeRate,
  key: CoinKey,
  value: string | number,
) => {
  const rate = rates[key];
  if (!rate) return '';

  const usdValue = (Number(rate) * Number(value)).toFixed(2);

  return `≈ ${usdValue}$`;
};

export const sliceString = (str: string, to?: number) =>
  `${str.substring(0, to ?? 25)}...`;

export const calculateTotal = (wallets: FoundWallet[], rates: ExchangeRate) => {
  const total = wallets.reduce((acc, w) => {
    const rate = rates[w.coinKey] ?? 0;
    const usd = Number(rate) * Number(w.amount);

    return acc + usd;
  }, 0);

  return total.toFixed(2);
};

export function formatCoinValue(value: number): string {
  if (Number.isInteger(value)) return `${value}.00`;
  // Regular expression to remove trailing zeros after the decimal point
  return value
    .toFixed(4)
    .toString()
    .replace(/(\.\d*?)0+$/, '$1')
    .replace(/\.$/, '');
}

export const calculateCoinTotal = (key: CoinKey, wallets: FoundWallet[]) => {
  const filteredWallets = wallets.filter((w) => w.coinKey === key);
  const total = filteredWallets.reduce((acc, w) => acc + w.amount, 0);
  return formatCoinValue(Number(total));
};

export const getRandomDelay = (minDelay: number, maxDelay: number) => {
  const minMs = minDelay * 1000;
  const maxMs = maxDelay * 1000;
  return Math.floor(Math.random() * (maxMs - minMs + 1)) + minMs;
};

export const getRandomChecksCount = () => {
  return (
    Math.floor(Math.random() * (MAX_CHECKS_COUNT - MIN_CHECKS_COUNT + 1)) +
    MIN_CHECKS_COUNT
  );
};

export const pickRandomWallet = (keys: CoinKey[]): CoinKey => {
  // Ensure the array is not empty
  if (keys.length === 0) {
    throw new Error('Array cannot be empty');
  }

  // Generate a random index
  const randomIndex = Math.floor(Math.random() * keys.length);

  // Return the element at the random index
  return keys[randomIndex];
};

export const getRandomWalletValue = (min: number, max: number) => {
  // Ensure min is less than max
  if (min > max) {
    throw new Error('min must be less than max');
  }

  // Generate a random value between min and max
  const randomValue = Math.random() * (max - min) + min;
  return parseFloat(randomValue.toFixed(3));
};
