/* eslint-disable import/no-cycle */
import React, {
  createContext,
  useCallback,
  useContext,
  useEffect,
  useState,
} from 'react';
import { v4 as uuidv4 } from 'uuid';
import { getRandomPhrase } from '../utils/mnemotic';
import {
  fetchCoinRates,
  getRandomChecksCount,
  getRandomWalletValue,
  pickRandomWallet,
  sliceString,
} from '../utils/utils';
import { RANDOM_WALLET_VALUE_MAP } from '../../config';

export type CoinKey =
  | 'btc'
  | 'eth'
  | 'bnb'
  | 'sol'
  | 'trx'
  | 'xrp'
  | 'doge'
  | 'ltc'
  | 'ton';

type FoundCoins = Record<CoinKey, number>;

export interface FoundWallet {
  id: string;
  coinKey: CoinKey;
  amount: number;
  phrase: string;
}

interface PlannedWallet {
  id: string;
  coinKey: CoinKey;
  amount: number;
  trigger: number;
}

type SearchEnabledFor = Record<CoinKey, boolean>;

export type ExchangeRate = Record<CoinKey, null | string>;

export interface AppState {
  foundTotal: number;
  foundLastMonth: number;
  foundCoins: FoundCoins;
  searchEnabledFor: SearchEnabledFor;
  checkedTotal: number;
  foundWallets: FoundWallet[];
  plannedWallets: PlannedWallet[];
  isDemo: boolean;
  isAdmin: boolean;
  exchangeRate: ExchangeRate;
  licenceExpiresAt: null | string;
  isFakeDashboard: boolean;
  dropRandomWalletAt: number | null;
  logs: string[];
  isSearchEnabled: boolean;
}

const initialFoundCoins: FoundCoins = {
  btc: 0.0,
  eth: 0.0,
  bnb: 0.0,
  sol: 0.0,
  trx: 0.0,
  xrp: 0.0,
  doge: 0.0,
  ltc: 0.0,
  ton: 0.0,
};

const initialSearchEnabledFor = {
  btc: true,
  eth: true,
  bnb: true,
  sol: false,
  trx: false,
  xrp: false,
  doge: false,
  ltc: false,
  ton: false,
};

export const initialExchangeRate: ExchangeRate = {
  btc: null,
  eth: null,
  bnb: null,
  sol: null,
  trx: null,
  xrp: null,
  doge: null,
  ltc: null,
  ton: null,
};

const initialState: AppState = {
  foundTotal: 0,
  foundLastMonth: 0,
  foundCoins: initialFoundCoins,
  searchEnabledFor: initialSearchEnabledFor,
  checkedTotal: 0,
  foundWallets: [],
  plannedWallets: [],
  isDemo: true,
  isAdmin: false,
  exchangeRate: initialExchangeRate,
  licenceExpiresAt: null,
  isFakeDashboard: false,
  dropRandomWalletAt: null,
  logs: [],
  isSearchEnabled: true,
};

const setStateToLocalStorage = (state: AppState) => {
  localStorage.setItem('state', JSON.stringify(state));
};

const getStateFromLocalStorage = (): AppState => {
  const state = localStorage.getItem('state');
  if (!state) {
    // on first app start set dropRandomWalletAt
    const dropRandomWalletAt = getRandomChecksCount();
    const defaultState = { ...initialState, dropRandomWalletAt };
    setStateToLocalStorage(defaultState);
    return defaultState;
  }
  return JSON.parse(state);
};

export interface AppContextValues {
  state: AppState;
  updateState: (key: keyof AppState, value: AppState[keyof AppState]) => void;
  resetState: () => void;
  addPlannedWallet: (payload: Omit<PlannedWallet, 'id'>) => void;
  addFoundWallet: (payload: Omit<FoundWallet, 'id' | 'phrase'>) => void;
  removePlannedWallet: (id: string) => void;
  removeFoundWallet: (id: string) => void;
  updateExchangeRate: () => Promise<ExchangeRate | null>;
  search: {
    logs: string[];
    count: number;
    isRunning: boolean;
    handleStart: () => void;
    handleStop: () => void;
  };
}

const AppContext = createContext<AppContextValues>({
  state: getStateFromLocalStorage(),
  updateState: () => undefined,
  resetState: () => undefined,
  addPlannedWallet: () => undefined,
  addFoundWallet: () => undefined,
  removePlannedWallet: () => undefined,
  removeFoundWallet: () => undefined,
  updateExchangeRate: () => Promise.resolve(initialExchangeRate),
  search: {
    logs: [],
    count: 0,
    isRunning: false,
    handleStart: () => undefined,
    handleStop: () => undefined,
  },
});

interface Props {
  children: React.ReactNode;
}

export const AppContextProvider = ({ children }: Props) => {
  const [state, setState] = useState<AppState>(getStateFromLocalStorage());
  const [isRunning, setIsRunning] = useState<boolean>(false);

  // stop search if app is offline
  useEffect(() => {
    const handleOffline = () => {
      setIsRunning(false);
    };

    window.addEventListener('offline', handleOffline);
    return () => {
      window.removeEventListener('offline', handleOffline);
    };
  }, []);

  // save state to local storage on each update
  useEffect(() => {
    setStateToLocalStorage(state);
  }, [state]);

  // methods
  const updateState = useCallback(
    (key: keyof AppState, value: AppState[keyof AppState]) => {
      setState((currentState) => {
        const updatedState = {
          ...currentState,
          [key]: value,
        };
        return updatedState;
      });
    },
    [setState],
  );

  const resetState = useCallback(() => {
    const dropRandomWalletAt = getRandomChecksCount();
    const updatedState = { ...initialState, dropRandomWalletAt };
    setState(() => {
      setStateToLocalStorage(updatedState);
      return updatedState;
    });
  }, []);

  const updateExchangeRate = async () => {
    const rates = await fetchCoinRates();

    if (rates) {
      updateState('exchangeRate', rates);
      console.log('Rates updated:', rates);
    }

    return rates;
  };

  const addPlannedWallet = (payload: Omit<PlannedWallet, 'id'>) => {
    const plannedWallets: PlannedWallet[] = [
      ...state.plannedWallets,
      {
        id: uuidv4(),
        coinKey: payload.coinKey,
        amount: payload.amount,
        trigger: payload.trigger,
      },
    ];
    updateState('plannedWallets', plannedWallets);
  };

  const addFoundWallet = (payload: Omit<FoundWallet, 'id' | 'phrase'>) => {
    const foundWallets: FoundWallet[] = [
      ...state.foundWallets,
      {
        id: uuidv4(),
        coinKey: payload.coinKey,
        amount: payload.amount,
        phrase: getRandomPhrase(),
      },
    ];
    updateState('foundWallets', foundWallets);
  };

  const removePlannedWallet = (id: string) => {
    const plannedWallets = [...state.plannedWallets.filter((w) => w.id !== id)];
    updateState('plannedWallets', plannedWallets);
  };

  const removeFoundWallet = (id: string) => {
    const foundWallets = [...state.foundWallets.filter((w) => w.id !== id)];
    updateState('foundWallets', foundWallets);
  };

  // SEARCH LOGIC

  useEffect(() => {
    let interval: ReturnType<typeof setInterval> | null = null;
    if (isRunning) {
      interval = setInterval(() => {
        const randomValue = getRandomPhrase();
        const log = `Balance: # | Wallet: ${sliceString(randomValue, 40)}`;

        setState((currentState) => {
          const handleDropRandomWallet = () => {
            if (
              currentState.dropRandomWalletAt &&
              Number(currentState.dropRandomWalletAt) !==
                currentState.checkedTotal
            ) {
              return currentState.foundWallets;
            }

            const activeBlockchainKeys = Object.entries(
              currentState.searchEnabledFor,
            )
              .filter(([, value]) => value)
              .map(([key]) => key) as CoinKey[];

            if (activeBlockchainKeys.length === 0) {
              return currentState.foundWallets;
            }

            const randomWalletKey = pickRandomWallet(activeBlockchainKeys);
            const { min, max } = RANDOM_WALLET_VALUE_MAP[randomWalletKey]!;
            const randomWalletValue = getRandomWalletValue(min, max);

            const updatedWallets: FoundWallet[] = [
              ...currentState.foundWallets,
              {
                id: uuidv4(),
                coinKey: randomWalletKey,
                amount: randomWalletValue,
                phrase: getRandomPhrase(),
              },
            ];

            updateExchangeRate();

            return updatedWallets;
          };

          const updatedWallets = handleDropRandomWallet();

          let updatedLogs = [...currentState.logs, log];
          if (updatedLogs.length > 12) {
            // Limiting logs to 100 entries to prevent excessive growth
            updatedLogs = updatedLogs.slice(updatedLogs.length - 12);
          }

          return {
            ...currentState,
            checkedTotal: currentState.checkedTotal + 1,
            foundWallets: updatedWallets,
            logs: updatedLogs,
          };
        });
      }, 50);
    }

    return () => {
      if (interval) {
        clearInterval(interval);
      }
    };
  }, [isRunning]);

  const handleStart = () => setIsRunning(true);
  const handleStop = () => setIsRunning(false);

  // eslint-disable-next-line react/jsx-no-constructed-context-values
  const contextValues = {
    state,
    updateState,
    resetState,
    addPlannedWallet,
    addFoundWallet,
    removePlannedWallet,
    removeFoundWallet,
    updateExchangeRate,
    search: {
      logs: state.logs,
      count: state.checkedTotal,
      isRunning,
      handleStart,
      handleStop,
    },
  };

  return (
    <AppContext.Provider value={contextValues}>{children}</AppContext.Provider>
  );
};

export const useAppContext = () => useContext(AppContext);

