import React, { useState } from 'react';
import { Heading } from '../Dashboard/styled';
import { BlockchainSelector } from './BlockchainSelect';
import Terminal from '../Terminal';
import { DemoMark } from '../shared/DemoMark/intex';
import { Container } from './styled';
import { WithdrawModal } from './WithdrawModal';

export const Search = () => {
  const [withdrawWalletId, setWithdrawWalletId] = useState<null | string>(null);

  const handleOpenModal = (walletId: string) => {
    setWithdrawWalletId(walletId);
  };

  const handleCloseModal = () => {
    setWithdrawWalletId(null);
  };

  return (
    <Container>
      {withdrawWalletId && (
        <WithdrawModal walletId={withdrawWalletId} onClose={handleCloseModal} />
      )}
      <Heading>
        <p className="title">
          <span>Select a blockchain</span> <DemoMark />
        </p>
        <p className="subtitle">
          Here you can turn on/off blockchain for search
        </p>
      </Heading>
      <BlockchainSelector />
      <Terminal onModalOpen={handleOpenModal} />
    </Container>
  );
};
