/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useRef, useState } from 'react';
import { Col, Row } from 'react-bootstrap';
import {
  Wrapper,
  TerminalContainer,
  Tile,
  Counter,
  FoundContainer,
  Buttons,
  Button,
  FoundItem,
  Thumb,
} from './styled';
import { IMAGES } from '../Search/BlockchainSelect';
import { blockchains } from '../../../config';
import { useAppContext } from '../../storage/context';
import { formatCoinValue, getUsdValue, sliceString } from '../../utils/utils';
import { TerminalBanner } from '../Banners/TerminalBannner';

interface Props {
  onModalOpen: (walletId: string) => void;
}

const Terminal = ({ onModalOpen }: Props) => {
  const { state, search } = useAppContext();
  const { logs, count, handleStart, handleStop, isRunning } = search;
  const endOfLogsRef = useRef<null | HTMLDivElement>(null);

  const [selectedWallet, setSelectedWallet] = useState<string | null>(null);

  const atLeastOneBlockchainSelected = Object.values(
    state.searchEnabledFor,
  ).some((v) => v);

  useEffect(() => {
    // stop search if wallet is found
    if (state.foundWallets.length > 0) {
      handleStop();
    }

    // auto select first found wallet
    if (!selectedWallet && state.foundWallets.length > 0) {
      setSelectedWallet(state.foundWallets[0].id);
    }
  }, [state.foundWallets]);

  const handleSelectWallet = (id: string) => {
    setSelectedWallet(id);
  };

  const handleWithdraw = () => {
    if (!selectedWallet) return;
    onModalOpen(selectedWallet);
  };

  const searchIsDisabled = state.foundWallets.length > 0;

  return (
    <Wrapper>
      <Row>
        <Col sm={6} style={{ paddingRight: '5px' }}>
          <Tile>
            <Counter>Checked: {count}</Counter>
            <TerminalContainer>
              {searchIsDisabled && <TerminalBanner />}
              <div className="terminal-body">
                {logs.map((log) => (
                  <div key={log} className="terminal-log">
                    {log}
                  </div>
                ))}
                <div ref={endOfLogsRef} />
              </div>
            </TerminalContainer>
            <Buttons>
              <Button
                className="stop"
                disabled={!isRunning}
                onClick={handleStop}
              >
                Stop
              </Button>
              <Button
                className="search"
                disabled={
                  isRunning || !atLeastOneBlockchainSelected || searchIsDisabled
                }
                onClick={handleStart}
              >
                Search
              </Button>
            </Buttons>
          </Tile>
        </Col>
        <Col sm={6} style={{ paddingLeft: '5px' }}>
          <Tile>
            <Counter>Found: {state.foundWallets.length}</Counter>
            <FoundContainer>
              {state.foundWallets.map((item) => {
                const isSelected = item.id === selectedWallet;
                const blockchain = blockchains[item.coinKey];
                const usdValue = getUsdValue(
                  state.exchangeRate,
                  item.coinKey,
                  item.amount,
                );

                return (
                  <FoundItem
                    key={item.id}
                    onClick={() => handleSelectWallet(item.id)}
                    style={{
                      ...(isSelected
                        ? { border: `1px solid ${blockchain.color}80` }
                        : {}),
                    }}
                  >
                    <Thumb src={IMAGES[item.coinKey]} />
                    <p className="text" style={{ color: blockchain.color }}>
                      {`${formatCoinValue(item.amount)} ${
                        blockchain.title
                      } ${usdValue} | ${sliceString(item.phrase)}`}
                    </p>
                    <div
                      className="bg"
                      style={{
                        background: blockchain.color,
                      }}
                    />
                  </FoundItem>
                );
              })}
            </FoundContainer>
            <Buttons>
              <Button
                className="withdraw"
                disabled={!selectedWallet}
                onClick={handleWithdraw}
              >
                Withdraw
              </Button>
            </Buttons>
          </Tile>
        </Col>
      </Row>
    </Wrapper>
  );
};

export default Terminal;
